import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { AppConstants } from '../app-constants';
import { User } from '../model/user';

@Injectable( {
  providedIn: 'root'
} )
export class UsuarioService {



  constructor ( private http: HttpClient ) {


  }

  getUsuarioList (): Observable<any> {

    return this.http.get<any>( AppConstants.baseUrl );

  }

  deleteUsuario ( id: Number ): Observable<any> {

    return this.http.delete( AppConstants.baseUrl + id, { responseType: 'text' } );


  }

  getUsuario ( id ): Observable<any> {

    return this.http.get<any>( AppConstants.baseUrl + id )
  }

  findUsuarioByNome ( nome: String ): Observable<any> {

    return this.http.get( AppConstants.baseUrl + "usuarioByNome/" + nome );
  }

    findUsuarioByLogin ( login: String ): Observable<any> {

    return this.http.get( AppConstants.baseUrl + "usuarioByLogin/" + login );
  }

  saveUsuario ( usuario: User ): Observable<any> {

    return this.http.post<any>( AppConstants.baseUrl, usuario );
  }

  updateUsuario ( usuario: User ): Observable<any> {

    return this.http.put<any>( AppConstants.baseUrl, usuario );
  }


  removeTelefone(id) : Observable<any>{

    return this.http.delete(AppConstants.baseUrl + "removerTelefone/" + id, { responseType: 'text' } );

  }


  userauthenticate () {
    if ( localStorage.getItem( 'token' ) !== null && localStorage.getItem( 'token' ).toString().trim() !== null )
    {

      return true;

    }
    else
    {
      return false ;

    }

  }

}
